<template>

	<div class="d-box v-box">
		<div class="headDiv" style="padding: 20px;">
			<div class="grid-content bg-purple searchleft">
				<div class="block">
					<span class="demonstration">查询日期</span>
					<el-date-picker v-model="timeValue" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期" :default-time="['00:00:00', '23:59:59']" value-format="yyyy-MM-dd HH:mm:ss">
					</el-date-picker>
				</div>
			</div>
			<div class="headerLeft searchleft">
				<span class="system">经销商</span>
				<el-input placeholder="请输入经销商" v-model="dealerName" clearable>
				</el-input>
			</div>
			<div class="grid-content bg-purple searchRight">
				<div>
					<SolidButton :type='"solid"' :text='"查询"' @click="search"></SolidButton>
					<SolidButton :type='"hollow"' :text='"重置"' @click="reset"></SolidButton>
				</div>
			</div>
		</div>
		<div class="flexTable">
			<Table :data='rows' :tableName='tableName' @selection-change="handleSelectionChange" :currentPage1="currentPage1" :num='num' @size-change="handleSizeChange" @current-change="handleCurrentChange"></Table>

		</div>
	</div>
</template>

<script>
	import Table from '../../table/Table.vue';
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';

	export default {
		components: {
			Table,
			SolidButton
		},
		data() {
			return {
				//头部
				timeValue: "",
				//列表
				rows: [],
				dealerName: '',
				multipleSelection: [],
				//分页
				currentPage1: 1,
				pageNum: 1,
				pageRows: 10, //每页
				num: 0, //总数
				isHidden: true,
				//表格对应的字段
				tableName: [{
						value: 'custname',
						name: "用户名"
					},
					{
						value: 'telephone',
						name: "手机号"
					},
					{
						value: 'dealerName',
						name: '所属主体'
					},
					{
						value: 'vin',
						name: '车辆VIN'
					},
					{
						value: 'selectTime',
						name: '查询时间'
					},
				]
			}
		},
		methods: {
			loadData() {
				var self = this;
				if(self.dealerName == '') {
					self.dealerName = null
				}
				var p = {
					pageNum: self.pageNum,
					pageRows: self.pageRows,
					begintime: self.timeValue[0],
					//					beginTime: null,
					endTime: self.timeValue[1],
					//										endTime: null,
					dealerName: self.dealerName
				}
				//1、列表接口
				self.$api.post('/crm/queryUserLog/queryLog', p, r => {
					self.rows = r.repData.list
					self.num = r.repData.total
				}, e => {
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})
			},
			// 查询
			search() {
				var self = this;
				//时间删除之后self.timeValue不存在不能用self.timeValue[0]，要给一个空值
				if(!self.timeValue) {
					self.timeValue = ""
				}
				self.loadData()
			},
			// 重置
			reset() {
				var self = this;
				self.dealerName = "";
				self.pageTotal = '';
				self.loadData()
			},
			// 点击行选中复选框
			toggleSelection(row) {
				// var rows = [];
				// rows.push(row);
				// if (rows) {
				//   rows.forEach(row => {
				//     this.$refs.multipleTable.toggleRowSelection(row);
				//   });
				// } else {
				//   this.$refs.multipleTable.clearSelection();
				// }
			},
			// 复选框选中的数据
			handleSelectionChange(val) {
				this.multipleSelection = val;
			},
			//分页
			handleSizeChange(val) {
				this.pageRows = val;
				this.loadData();
			},
			handleCurrentChange(val) {
				this.pageNum = val;
				this.loadData();
			}
		},
		created: function() {
			this.loadData();
		}
	}
</script>

<style scoped="scoped">
	#container {
		width: 200px;
		height: 300px;
	}
	
	.fotterPacg {
		padding: 26px 0px;
	}
	
	.submitStyle {
		width: 84px;
		height: 37px;
		background: #E05D6F;
		border-radius: 15px;
		border: #E05D6F;
	}
	
	.submitStyle:hover {
		border-color: #E05D6F;
	}
	
	.cancelStyle {
		width: 84px;
		height: 37px;
		border: 1px solid #F4516B;
		border-radius: 15px;
		color: #F4516B;
	}
	
	.cancelStyle:hover {
		background-color: #ffffff;
	}
	
	.grid-content {
		min-height: 36px;
	}
	
	.row-bg {
		padding: 10px 0;
		background-color: #f9fafc;
	}
	
	.block {
		display: inline-block;
	}
	/*两种清除浮动方法
    1:给父元素添加overflow:hidden
    2:给父元素加伪类*/
	
	.header {
		padding: 20px;
		/*overflow: hidden;
        zoom: 1;*/
	}
	
	.header:after {
		clear: both;
		content: '';
		display: block;
		width: 0;
		height: 0;
		visibility: hidden;
	}
	
	.search {
		border-color: #d9524f;
		background-color: #d9524f;
		color: #fff;
		width: 90px;
	}
	
	.reset {
		border-color: #d9524f;
		color: #d9524f;
		width: 90px;
	}
	
	.searchleft {
		width: 430px;
		float: left;
	}
	
	.system {
		display: inherit;
		line-height: 40px;
		width: 20%;
		float: left;
	}
	
	.searchRight {
		float: right;
	}
	
	.el-input {
		width: 188px;
		height: 37px;
	}
	
	.box {
		width: 100%;
		height: 100%;
		background-color: #fff;
	}
	
	.main {
		padding: 0 20px;
	}
	
	.clickStyle {
		color: red;
	}
	
	.detailRow {
		display: inline-block;
		width: 48%;
	}
	
	.detailLongRow {
		display: inline-block;
	}
	
	.detailRow span:first-of-type {
		width: 70px;
		display: inline-block;
	}
	
	.detailRows span:first-of-type {
		width: 70px;
		display: inline-block;
		float: left;
	}
	
	.m_textarea {
		width: 312px;
		resize: none;
		min-height: 60px;
	}
	
	.detailMessage {
		padding: 6px 0px;
	}
	
	.replyClass1 {}
	
	.replyClass2 {
		background: #f9f9f9;
		padding: 5px;
		color: #999;
		border: none;
	}
	
	.d-box {
		display: -moz-box;
		/* OLD - Firefox 19- (buggy but mostly works) */
		display: -webkit-box;
		/* OLD - iOS 6-, Safari 3.1-6 */
		display: -webkit-flex;
		/* NEW - Chrome */
		display: -ms-flexbox;
		/* TWEENER - IE 10 */
		display: box;
		display: flexbox;
		display: flex;
		/* NEW, Spec - Opera 12.1, Firefox 20+ */
		height: 100%;
		width: 100%;
	}
	
	.v-box {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-moz-box-orient: vertical;
		-moz-box-direction: normal;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	
	.headDiv {
		/*padding: 20px;*/
		background: #fff;
		overflow: hidden;
		position: relative;
	}
	
	.flexTable {
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
	}
	
	.tableDiv {
		width: 100%;
		height: 100%;
		position: absolute;
		overflow: auto;
	}
	
	.footerDiv {
		height: 40px;
		position: relative;
	}
</style>


